$color-bg: #e4ebf5;

$color-default: #222;
$color-red: #d44439;
$color-gray: #666;
$color-light-gray: #999;
$color-deep-white: #e4e4e4;
$color-gold: #bba8a8;
$color-primary: #2979ff;
$color-primary-light: #82b1ff;

$font-size-default: 14px;
$font-size-max: 24px;
$font-size-bigger: 20px;
$font-size-large: 18px;
$font-size-normal: 16px;
$font-size-mini: 12px;

$padding-window: 20px;

@mixin fade {
  &:global(.fade-enter) {
    opacity: 0;
    transform: translateX(100%);
  }

  &:global(.fade-enter-active) {
    opacity: 1;
    transform: translateX(0);
    transition: all 500ms;
  }

  &:global(.fade-exit) {
    opacity: 1;
    transform: translateX(0);
  }

  &:global(.fade-exit-active) {
    opacity: 0;
    transform: translateX(-100%);
    transition: all 500ms;
  }
}

@mixin fly {
  &:global(.fly-enter),
  &:global(.fly-appear) {
    transform: translate3d(100%, 0, 0);
  }
  &:global(.fly-enter-active),
  &:global(.fly-appear-active) {
    transition: transform 0.3s;
    transform: translate3d(0, 0, 0);
  }
  &:global(.fly-exit-active) {
    transition: transform 0.3s;
    transform: translate3d(100%, 0, 0);
  }
}

.mini-player {
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  padding: 0 10px;
  width: 100%;
  height: 60px;
  background-color: $color-bg;
  z-index: 1001;
  &:global(.mini-enter),
  &:global(.mini-appear) {
    transform: translate3d(0, 100%, 0);
  }
  &:global(.mini-enter-active),
  &:global(.mini-appear-active) {
    transition: transform 0.4s;
    transform: translate3d(0, 0, 0);
  }
  &:global(.mini-exit) {
    transform: translate3d(0, 0, 0);
  }
  &:global(.mini-exit-active) {
    transition: transform 0.4s;
    transform: translate3d(0, 100%, 0);
  }
  .img-wrap {
    margin-right: 10px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
    animation: spin 15s infinite linear;
    &.pause {
      animation-play-state: paused;
    }
  }
  .text {
    flex: 1;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    .name {
      margin-bottom: 5px;
    }
    .singer {
      font-size: $font-size-mini;
      color: $color-light-gray;
      margin-bottom: 0;
    }
  }
  .control {
    margin-left: 10px;

    .control-button {
      color: $color-primary-light;
    }
    .control-list {
      margin-left: 10px;
      border-radius: 50%;
      color: $color-primary-light;
    }
  }

  @keyframes spin {
    0% {
      transform: rotate(0);
    }
    100% {
      transform: rotate(360deg);
    }
  }
}
