$color-bg: #e4ebf5;

$color-default: #222;
$color-red: #d44439;
$color-gray: #666;
$color-light-gray: #999;
$color-deep-white: #e4e4e4;
$color-gold: #bba8a8;
$color-primary: #2979ff;
$color-primary-light: #82b1ff;

$font-size-default: 14px;
$font-size-max: 24px;
$font-size-bigger: 20px;
$font-size-large: 18px;
$font-size-normal: 16px;
$font-size-mini: 12px;

$padding-window: 20px;

@mixin fade {
  &:global(.fade-enter) {
    opacity: 0;
    transform: translateX(100%);
  }

  &:global(.fade-enter-active) {
    opacity: 1;
    transform: translateX(0);
    transition: all 500ms;
  }

  &:global(.fade-exit) {
    opacity: 1;
    transform: translateX(0);
  }

  &:global(.fade-exit-active) {
    opacity: 0;
    transform: translateX(-100%);
    transition: all 500ms;
  }
}

@mixin fly {
  &:global(.fly-enter),
  &:global(.fly-appear) {
    transform: translate3d(100%, 0, 0);
  }
  &:global(.fly-enter-active),
  &:global(.fly-appear-active) {
    transition: transform 0.3s;
    transform: translate3d(0, 0, 0);
  }
  &:global(.fly-exit-active) {
    transition: transform 0.3s;
    transform: translate3d(100%, 0, 0);
  }
}

.fullscreen-player {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  z-index: 1001;
  background-color: $color-bg;
  justify-content: space-between;
  &:global(.full-enter),
  &:global(.full-exit-done) {
    opacity: 0;
  }
  &:global(.full-enter-active) {
    opacity: 1;
    transition: all 0.4s;
  }
  &:global(.full-exit-active) {
    opacity: 0;
    transition: all 0.4s;
  }
  .top {
    display: flex;
    flex-shrink: 0;
    align-items: center;
    padding-right: 10px;
    width: 100%;
    height: 50px;
    border-bottom: 1px solid rgba(228, 228, 228, 0.1);
    .back {
      width: 40px;
      height: 100%;
      line-height: 50px;
      text-align: center;
      color: $color-primary-light;
      i {
        font-size: 30px;
      }
    }
    .song-info {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      .song-name {
        margin-bottom: 2px;
      }
      .singer-name {
        color: $color-gray;
        font-size: small;
        margin-bottom: 0;
      }
    }
  }
}
