$color-bg: #e4ebf5;

$color-default: #222;
$color-red: #d44439;
$color-gray: #666;
$color-light-gray: #999;
$color-deep-white: #e4e4e4;
$color-gold: #bba8a8;
$color-primary: #2979ff;
$color-primary-light: #82b1ff;

$font-size-default: 14px;
$font-size-max: 24px;
$font-size-bigger: 20px;
$font-size-large: 18px;
$font-size-normal: 16px;
$font-size-mini: 12px;

$padding-window: 20px;

@mixin fade {
  &:global(.fade-enter) {
    opacity: 0;
    transform: translateX(100%);
  }

  &:global(.fade-enter-active) {
    opacity: 1;
    transform: translateX(0);
    transition: all 500ms;
  }

  &:global(.fade-exit) {
    opacity: 1;
    transform: translateX(0);
  }

  &:global(.fade-exit-active) {
    opacity: 0;
    transform: translateX(-100%);
    transition: all 500ms;
  }
}

@mixin fly {
  &:global(.fly-enter),
  &:global(.fly-appear) {
    transform: translate3d(100%, 0, 0);
  }
  &:global(.fly-enter-active),
  &:global(.fly-appear-active) {
    transition: transform 0.3s;
    transform: translate3d(0, 0, 0);
  }
  &:global(.fly-exit-active) {
    transition: transform 0.3s;
    transform: translate3d(100%, 0, 0);
  }
}

.song-list {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1002;
  &:global(.move-enter),
  &:global(.move-appear) {
    opacity: 0;
    // .list-wrap {
    //   transform: translateY(100%);
    // }
  }
  &:global(.move-enter-active),
  &:global(.move-appear-active) {
    opacity: 1;
    transition: opacity 0.3s;
    // .list-wrap {
    //   transform: translateY(0);
    //   transition: transform 0.3s;
    // }
  }
  &:global(.move-exit) {
    opacity: 1;
    // .list-wrap {
    //   transform: translateY(0);
    // }
  }

  &:global(.move-exit-active) {
    opacity: 0;
    transition: opacity 0.3s;
    // .list-wrap {
    //   transform: translateY(100%);
    //   transition: transform 0.3s;
    // }
  }

  .mask {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
  }
  .list-wrap {
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 0 20px;
    width: 100%;
    background-color: $color-bg;
    border-radius: 10px 10px 0 0;
    opacity: 1;
    .list-header {
      padding: 10px 0;
    }
    .list-scroll-wrap {
      height: 50vh;
      overflow: hidden;
      .song-item {
        color: $color-primary-light;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 5px 5px 5px 26px;
        p {
          margin-bottom: 0;
        }

        .delete {
          flex-shrink: 0;
          font-size: small;
        }
        &.active {
          padding-left: 0;
          span {
            padding-right: 10px;
          }
        }
      }
    }
  }
}
